/* Professional animated nav links */
.nav-link-animated {
  position: relative;
  display: inline-block;
  color: inherit;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
  overflow: visible;
  padding-bottom: 4px;
}

.nav-link-animated::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background: #10b981;
  transition: width 0.3s cubic-bezier(0.34, 1.56, 0.64, 1);
}

.nav-link-animated:hover {
  color: #10b981;
}

.nav-link-animated:hover::after {
  width: 100%;
}

.nav-link-animated.active {
  color: #10b981;
}

.nav-link-animated.active::after {
  width: 100%;
  background: #10b981;
}

/* Dark mode support */
.dark .nav-link-animated:hover {
  color: #6ee7b7;
}

.dark .nav-link-animated:hover::after {
  background: #6ee7b7;
}

.dark .nav-link-animated.active {
  color: #6ee7b7;
}

.dark .nav-link-animated.active::after {
  background: #6ee7b7;
}

/* Smooth button transitions */
button {
  -webkit-tap-highlight-color: transparent;
}

/* Remove default focus outline */
a:focus {
  outline: none;
}

/* Remove focus outline on touch devices */
@media (hover: none) {
  .nav-link-animated:focus-visible::after {
    width: 100%;
  }
}

/* Mobile navigation optimizations */
@media (max-width: 1024px) {
  .nav-link-animated {
    font-size: 0.95rem;
  }
}

@media (max-width: 640px) {
  .nav-link-animated {
    font-size: 0.875rem;
    padding-bottom: 2px;
  }
  
  .nav-link-animated::after {
    height: 1px;
  }
}